import React, { Component } from 'react';
import ShowList from './showList';



let noOfTasks;
let lslist = [];

class ToDoList extends Component {

    constructor(props){
        super(props);

        // localStorage.removeItem('totalSum');
        if (JSON.parse(localStorage.getItem('list')) === null || JSON.parse(localStorage.getItem('list')).length === 0){
            lslist = [];
            noOfTasks = 0;
        }
        else{
            lslist = JSON.parse(localStorage.getItem('list'));
        }

        let totalSum = localStorage.getItem('totalSum');

        if (totalSum === null){
            totalSum = 0;
        }
        else{
            totalSum = parseInt(totalSum);
        }

        let startedSum = localStorage.getItem('startedSum');
        if (startedSum === null){
            startedSum = 0;
        }
        else{
            startedSum = parseInt(startedSum);
        }
        

        this.state = {
            inputStr : '',
            inputStr_money: '',
            list: lslist,
            addError: false,
            totalSum: totalSum,
            startedSum: startedSum
        };

        
        if (noOfTasks != 0)
            noOfTasks = this.state.list[this.state.list.length-1].number;

        //console.log(this.state.list);

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleInputChange_money = this.handleInputChange_money.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleDelete= this.handleDelete.bind(this);
        this.handleEdit = this.handleEdit.bind(this);
        this.handleUpdate = this.handleUpdate.bind(this);
        this.handleStart= this.handleStart.bind(this);
        this.handleFinished= this.handleFinished.bind(this);
        this.getFormattedTime = this.getFormattedTime.bind(this);
    }
    

    getFormattedTime(){

        let today = new Date();
        let month = today.getMonth()+1;
        let day = today.getDate();
        let hour = today.getHours();
        let minute = today.getMinutes();
        let seconds = today.getSeconds();

        if (month < 10){
            month = '0' + month;
        }
        if (day < 10){
            day = '0' + day;
        }
        if (hour < 10){
            hour = '0' + hour;
        }
        if (minute < 10){
            minute = '0' + minute;
        }
        if (seconds < 10){
            seconds = '0' + seconds;
        }

        let date = today.getFullYear()+'/'+ month +'/'+ day +' '+ hour +':'+ minute + ':' + seconds;

        return date;

    }

    handleEdit(obj){

        let array = this.state.list;
        obj.status = "edit-task";
        obj.display = "none";
        this.setState({
            list: array
        });

        localStorage.setItem('list', 
            JSON.stringify(this.state.list)
        );
        //console.log("Edit", obj);
    }


    handleUpdate(obj, str){

        let array = this.state.list;

        let date = this.getFormattedTime();
        
        let pushObj = {
                       "date": date, 
                       "newString": str,
                       "oldString": obj.string
                      };
        obj.history.push(pushObj);
        obj.status = "waiting";
        obj.string = str;
        obj.display = "show";

        this.setState({
            list: array
        });

        localStorage.setItem('list', 
            JSON.stringify(this.state.list)
        );
        //console.log("Updated", obj);
    }

    handleDelete(obj){
        let array = this.state.list;
        const index = array.indexOf(obj);
        array.splice(index, 1);

        let modification;
        if (obj.status !== "start-task"){
            modification = this.state.startedSum;
        }
        else{
            modification = this.state.startedSum - obj.money;
        }

        this.setState({
            list: array,
            totalSum: this.state.totalSum - obj.money,
            startedSum: modification
        });

        localStorage.setItem('list', 
            JSON.stringify(this.state.list)
        );

        localStorage.setItem('totalSum', 
          this.state.totalSum - obj.money
        );

        localStorage.setItem('startedSum', 
            modification
        );
        //console.log("Deleting", obj);
    }

    handleStart(obj){

        if (obj.status === "start-task") return;
        let array = this.state.list;
        obj.status = "start-task";
        this.setState({
            list: array,
            startedSum: this.state.startedSum + parseInt(obj.money)
        });

        localStorage.setItem('startedSum', JSON.stringify((this.state.startedSum + parseInt(obj.money))));
        localStorage.setItem('list', 
            JSON.stringify(this.state.list)
        );
    }

    handleFinished(obj){
        let array = this.state.list;
        
        obj.status = "finish-task";
        this.setState({
            list: array,
            startedSum: this.state.startedSum - obj.money
        });


        localStorage.setItem('startedSum', JSON.stringify((this.state.startedSum - obj.money)));
        localStorage.setItem('list', 
            JSON.stringify(this.state.list)
        );
        //console.log("Finished", obj);
    }

    handleInputChange(event){
        this.setState({
            inputStr: event.target.value,
            addError: false
        });
    }

    handleInputChange_money(event){

        const string = event.target.value;
        let error = true;
        
        if (/[0-9]/.test(string) === true  && ((/[a-z]/.test(string)) === false) && (/[A-Z]/.test(string)) === false  ){
            error = false;
        }
        
        this.setState({
            inputStr_money: event.target.value,
            addError: error
        });
    }

    handleClick(){


        if ((this.state.inputStr.length !== 0 && this.state.inputStr_money.length !== 0) && this.state.addError === false){
            noOfTasks++;

            let date = this.getFormattedTime();
            // console.log(date);
            this.setState({
                list:[...this.state.list,
                     {"number" : noOfTasks, "string": this.state.inputStr, 
                     "status": "waiting", "display": "show", "history": [], "addedTime": date, "money": this.state.inputStr_money}
                    ],
                inputStr: "",
                inputStr_money: "",
                totalSum: this.state.totalSum + parseInt(this.state.inputStr_money)
            });

            localStorage.setItem('list', 
                JSON.stringify([...this.state.list,
                     {"number" : noOfTasks, "string": this.state.inputStr, 
                     "status": "waiting", "display": "show", "history": [], "addedTime": date, "money": this.state.inputStr_money}
                ])
            );

            localStorage.setItem('totalSum', JSON.stringify((this.state.totalSum + parseInt(this.state.inputStr_money))));
        }
        else{
            this.setState({addError: true});
        }
    }

    render(){

        const {inputStr, inputStr_money, addError, totalSum, list, startedSum} = this.state;

        return (
            <div className = "todolist">

                <div className="display-sum">
                    <div className="started">Started amount: {startedSum} lei</div>
                    <div className="total">Total amount: {totalSum} lei</div>
                </div>
                <header>
                    
                    <h1>Wish List</h1>
                    <div>
                    <div>
                        <input 
                            className="search-input"
                            onChange = {this.handleInputChange} 
                            value = {inputStr}
                            type = "text"
                            placeholder = "Add Wish" 
                            
                          />
                          <input 
                            className="money-input"
                            onChange = {this.handleInputChange_money} 
                            value = {inputStr_money}
                            type = "text"
                            placeholder = "$$$ lei" 
                            
                          />
                    
                      <button onClick = {this.handleClick} type="button">Add Wish!</button>
                      {addError === true && <span className="addError"> Please complete corectly all fields!</span>}


                    </div>  
                    </div>
                </header>
                
                <div> 

               {list.map(obj =>{  
                   if (obj){
                        return(
                            <ShowList 
                                key = {obj.number} 
                                obj = {obj} 
                                history = {obj.history}
                                statusClass = {obj.status}
                                display = {obj.display}
                                update = {(obj, str) => {this.handleUpdate(obj, str);}}
                                edit = {(obj) => {this.handleEdit(obj);}}
                                delete = {(obj) => {this.handleDelete(obj);}}
                                start = {(obj) => {this.handleStart(obj);}}
                                finished = {(obj) => {this.handleFinished(obj);}}
                                addedTime={obj.addedTime}
                                money={obj.money}
                            />
                        );
                    }
                })} 
                </div>
            </div>       
        );
    }
}
export default ToDoList;


