import React, {Component} from 'react';
import { shape, string, func, array } from 'prop-types';


class ShowList extends Component {
    constructor(props) {
        super(props);

        this.state = {
            inputStr: this.props.obj.string,
        };
        
        this.handleEditChange = this.handleEditChange.bind(this);


    }

    handleEditChange(event){
        this.setState({
            inputStr: event.target.value
        });
    }
    
    render() {
        const {statusClass} = this.props;
        const {status} = this.props.obj;
        const {display, history, addedTime, money} = this.props;
    

        const historyList = history.map((obj) =>
            <li key = {obj.date}> {obj.oldString} <i className="fas fa-hand-point-right"/> {obj.newString} at {obj.date.slice(0, obj.date.length-3)}</li>
        );
        
            return (    
                <div className="wrapper">
                    <div>
                        <div className="taskName"> 
                            
                            <div className="A">
                                <p 
                                    style =  {{display : display === "show"? "block" : "none"}} 
                                    className = {statusClass}>
                                    {this.props.obj.string}
                                </p>
                                <p><i className="fas fa-money-bill-alt"/></p>
                                <p className = {statusClass}> {money} lei </p>
                                <div 
                                    style={{display : display === "show"? "block" : "none"}}
                                    className={status === 'waiting' ? 'xMark' : status === 'start-task' ? 'inProgress' : 'checkMark'} 
                                />
                                <input 
                                    style={{display : display === "show" ? "none" : "block"}}
                                    className="edit-task"
                                    type="text"
                                    value={this.state.inputStr}
                                    placeholder={this.props.obj.string}
                                    onChange={this.handleEditChange} 
                                />
                                <button 
                                    style = {{display : display === "show" ? "none" : "block"}}
                                    onClick = {() => this.props.update(this.props.obj, this.state.inputStr)}
                                    className = "updateButton" type="button">Update Task
                                </button>

                                 <div className="addedTime">Wish added on: {addedTime} </div>
                            </div>
                            
                            {history.length !== 0 && <div className="B">
                                <h4>History:</h4>
                                <ul>{historyList}</ul>
                            </div>
                        }

                                                

                        </div>
                        
                        <button onClick = {() => this.props.edit(this.props.obj)} type="button">Edit Wish</button>

                        <button onClick = {() => this.props.delete(this.props.obj)}  type="button">Delete Wish</button> 
                        <button onClick = {() => this.props.start(this.props.obj)} type="button">Start Wish</button>
                        <button onClick = {() => this.props.finished(this.props.obj)} type="button">Finish Wish</button>      
                    </div>  
                </div>
            );
        
    }
}

ShowList.propTypes = {
    obj: shape({ 
        string: string.isRequired,
        status: string.isRequired,
        display: string.isRequired
    }).isRequired,  
    addedTime: string.isRequired,
    history: array.isRequired,
    display: string.isRequired,
    statusClass: string.isRequired,
    edit: func.isRequired,
    update:func.isRequired,
    delete: func.isRequired,
    finished: func.isRequired,
    start: func.isRequired,
    money: string.isRequired
};
export default ShowList;




